<template>
  <div class="ProgrammingBackground">
    <table>
      <thead>
        <tr>
          <th style="width: 2%">学号</th>
          <th style="width: 3%">姓名</th>
          <th style="width: 4%">C语言代码量</th>
          <th style="width: 5%">数据结构代码量</th>
          <th style="width: 3%">Java代码量</th>
          <th style="width: 3%">JavaWeb代码量</th>
          <th style="width: 4%">Python代码量</th>
          <th style="width: 3%">OJ刷题数</th>
          <th style="width: 3%">其他刷题数</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in ProgrammingTable" :key="item.id">
          <td>{{ item.studentNumber }}</td>
          <td>{{ item.studentName }}</td>
          <td>{{ item.clanguageCodeVolume }}</td>
          <td>{{ item.dataStructureCodeVolume }}</td>
          <td>{{ item.javaCodeVolume }}</td>
          <td>{{ item.javaWebCodeVolume }}</td>
          <td>{{ item.pythonCodeVolume }}</td>
          <td>{{ item.ojPracticeNumber }}</td>
          <td>{{ item.otherPracticeNumber }}</td>
        </tr>
      </tbody>
    </table>
  </div>
  <div class="ProgrammingPagination">
    <el-pagination
      size="small"
      :page-size="pageSize"
      :pager-count="10"
      background
      layout="prev, pager, next"
      :total="total"
      @current-change="handlePageChange"
    />
  </div>
</template>

<script>
import axios from "@/axios";
export default {
  data() {
    return {
      ProgrammingTable: [],
      currentPage: 1,
      pageSize: 15,
      total: 130,
    };
  },

  mounted() {
    this.FetchData();
  },
  methods: {
    handlePageChange(page) {
      this.currentPage = page;
      this.FetchData();
    },

    async FetchData() {
      try {
        const informationResponse = await axios.get(
          "/admin/getAcademicPerformance/getProgrammingCapabilities",
          {
            params: {
              page: this.currentPage,
              pageSize: this.pageSize,
            },
          }
        );
        if (informationResponse.code == 1) {
          /*  this.total = informationResponse.data.total; */
          this.ProgrammingTable = informationResponse.data.records;
        } else {
          this.$message("数据加载失败!");
        }
      } catch (error) {
        this.$message("数据加载失败!");
        console.error("报错原因：", error);
      }
    },
  },
};
</script>

<style>
.ProgrammingPagination {
  position: absolute;
  top: 94%;
}
.ProgrammingBackground {
  position: relative;
  overflow: auto;
  width: 100%;
  height: 95%;
}

@import "/src/components/ManageSystem/TableStyle.css";
</style>
